import "./style.scss";

import { Button, Card, Classes, Intent, RangeSlider } from "@blueprintjs/core";
import {
  COOLING_DEFAULT,
  COOLING_MAX,
  DEADBAND_DEFAULT,
  DEADBAND_MAX,
  DEADBAND_MIN,
  HEATING_DEFAULT,
  HEATING_MIN,
  SETPOINT_DEFAULT,
  SETPOINT_MAX,
  SETPOINT_MIN,
  createSetpointLabel,
  isSetpointDelete,
} from "./util";
import {
  IFilter,
  ISetpoint,
  createSetpoint,
  deleteSetpoint,
  filterSetpoints,
  readSetpoints,
  selectFilterSetpoints,
  selectReadSetpoints,
  updateSetpoint,
} from "controllers/setpoints/action";
import { get, merge } from "lodash";

import { Header } from "components";
import { IconNames } from "@blueprintjs/icons";
import React from "react";
import { RootProps } from "routes";
import { connect } from "react-redux";
import { DeepPartial } from "../../utils/types";

interface SetpointProps extends RootProps {
  readSetpoints: () => void;
  filterSetpoints: (payload: IFilter) => void;
  createSetpoint: (payload: DeepPartial<ISetpoint>) => void;
  updateSetpoint: (payload: DeepPartial<ISetpoint>) => void;
  deleteSetpoint: (payload: number) => void;
  setpoints?: ISetpoint[];
  filtered?: ISetpoint[];
}

class Setpoint extends React.Component<SetpointProps, any> {
  constructor(props: SetpointProps) {
    super(props);
    this.state = {
      label: createSetpointLabel("all", {
        setpoint: SETPOINT_DEFAULT,
        deadband: DEADBAND_DEFAULT,
        heating: HEATING_DEFAULT,
        cooling: COOLING_DEFAULT,
      }),
      setpoint: SETPOINT_DEFAULT,
      deadband: DEADBAND_DEFAULT,
      heating: HEATING_DEFAULT,
      cooling: COOLING_DEFAULT,
      editing: null,
    };
  }

  componentDidMount() {
    this.props.readSetpoints();
  }

  handleChange = (field: "label" | "setpoint" | "deadband" | "value", setpoint?: DeepPartial<ISetpoint>) => {
    return (value: any) => {
      const { editing } = this.state;
      switch (field) {
        case "label":
          return this.setState(
            setpoint === undefined ? { label: value } : { editing: merge(editing, { label: value }) }
          );
        case "setpoint":
          return this.setState(
            setpoint === undefined
              ? {
                  label: createSetpointLabel("all", {
                    setpoint: (value[0] + value[1]) / 2,
                    deadband: get(editing, "deadband", get(setpoint, "deadband")),
                    heating: get(editing, "heating", get(setpoint, "heating")),
                    cooling: get(editing, "cooling", get(setpoint, "cooling")),
                  }),
                  setpoint: (value[0] + value[1]) / 2,
                }
              : {
                  editing: merge(editing, {
                    label: createSetpointLabel("all", {
                      setpoint: (value[0] + value[1]) / 2,
                      deadband: get(editing, "deadband", get(setpoint, "deadband")),
                      heating: get(editing, "heating", get(setpoint, "heating")),
                      cooling: get(editing, "cooling", get(setpoint, "cooling")),
                    }),
                    setpoint: (value[0] + value[1]) / 2,
                  }),
                }
          );
        case "deadband":
          return this.setState(
            setpoint === undefined
              ? {
                  label: createSetpointLabel("all", {
                    setpoint: get(editing, "setpoint", get(setpoint, "setpoint")),
                    deadband: value,
                    heating: get(editing, "heating", get(setpoint, "heating")),
                    cooling: get(editing, "cooling", get(setpoint, "cooling")),
                  }),
                  deadband: value,
                }
              : {
                  editing: merge(editing, {
                    label: createSetpointLabel("all", {
                      setpoint: get(editing, "setpoint", get(setpoint, "setpoint")),
                      deadband: value,
                      heating: get(editing, "heating", get(setpoint, "heating")),
                      cooling: get(editing, "cooling", get(setpoint, "cooling")),
                    }),
                    deadband: value,
                  }),
                }
          );
        case "value":
          return this.setState(
            setpoint === undefined
              ? {
                  label: createSetpointLabel("all", {
                    setpoint: get(editing, "setpoint", get(setpoint, "setpoint")),
                    deadband: get(editing, "deadband", get(setpoint, "deadband")),
                    heating: value[0],
                    cooling: value[1],
                  }),
                  heating: value[0],
                  cooling: value[1],
                }
              : {
                  editing: merge(editing, {
                    label: createSetpointLabel("all", {
                      setpoint: get(editing, "setpoint", get(setpoint, "setpoint")),
                      deadband: get(editing, "deadband", get(setpoint, "deadband")),
                      heating: value[0],
                      cooling: value[1],
                    }),
                    heating: value[0],
                    cooling: value[1],
                  }),
                }
          );
      }
    };
  };

  handleCreate = () => {
    const { label, heating, cooling } = this.state;
    this.props.createSetpoint({ label, heating, cooling });
    this.setState({
      label: createSetpointLabel("all", {
        setpoint: SETPOINT_DEFAULT,
        deadband: DEADBAND_DEFAULT,
        heating: HEATING_DEFAULT,
        cooling: COOLING_DEFAULT,
      }),
      setpoint: SETPOINT_DEFAULT,
      deadband: DEADBAND_DEFAULT,
      heating: HEATING_DEFAULT,
      cooling: COOLING_DEFAULT,
      editing: null,
    });
  };

  handleEdit = (setpoint: DeepPartial<ISetpoint>) => {
    const { id, label, heating, cooling } = setpoint;
    this.setState({ editing: { id, label, heating, cooling } });
  };

  handleCancel = () => {
    this.setState({ editing: null });
  };

  handleSave = () => {
    const { id, label, heating, cooling } = this.state?.editing || {};
    this.props.updateSetpoint({ id, label, heating, cooling });
    this.setState({ editing: null });
  };

  handleDelete = (setpoint: DeepPartial<ISetpoint>) => {
    const { id } = setpoint;
    if (id !== undefined) {
      this.props.deleteSetpoint(id);
    }
  };

  render() {
    const { filtered } = this.props;
    const { label, setpoint, deadband, heating, cooling, editing } = this.state;
    return (
      <div className={"setpoint"}>
        <Header {...this.props} />
        <h2>Create Setpoint</h2>
        <div className="create">
          <Card interactive>
            <div>
              <input
                className={Classes.INPUT}
                type="text"
                value={label}
                onChange={(e) => this.handleChange("label")(e.target.value)}
              />
            </div>
            <div>
              <input
                className={Classes.INPUT}
                type="number"
                min={DEADBAND_MIN}
                max={DEADBAND_MAX}
                value={deadband}
                onChange={(e) => this.handleChange("deadband")(e.target.value)}
              />
            </div>
            <div>
              <RangeSlider
                min={SETPOINT_MIN}
                max={SETPOINT_MAX}
                stepSize={0.5}
                labelStepSize={5}
                value={[setpoint - deadband / 2, setpoint + deadband / 2]}
                onChange={this.handleChange("setpoint")}
              />
            </div>
            <div>
              <RangeSlider
                min={HEATING_MIN}
                max={COOLING_MAX}
                stepSize={1}
                labelStepSize={5}
                value={[heating, cooling]}
                onChange={this.handleChange("value")}
              />
            </div>
            <div>
              <Button icon={IconNames.ADD_TO_ARTIFACT} intent={Intent.PRIMARY} minimal onClick={this.handleCreate} />
            </div>
          </Card>
        </div>
        <h1>Setpoints</h1>
        <div className="list">
          {filtered?.map((setpoint, i) => {
            return setpoint.id === editing?.id ? (
              <Card key={setpoint.id || i} interactive>
                <div>
                  <input
                    className={Classes.INPUT}
                    type="text"
                    value={editing.label}
                    onChange={(e) => this.handleChange("label", setpoint)(e.target.value)}
                  />
                </div>
                <div>
                  <input
                    className={Classes.INPUT}
                    type="number"
                    value={editing.deadband}
                    onChange={(e) => this.handleChange("deadband", setpoint)(e.target.value)}
                  />
                </div>
                <div>
                  <RangeSlider
                    min={SETPOINT_MIN}
                    max={SETPOINT_MAX}
                    stepSize={0.5}
                    labelStepSize={5}
                    value={[editing.setpoint - deadband / 2, editing.setpoint + deadband / 2]}
                    onChange={this.handleChange("setpoint", setpoint)}
                  />
                </div>
                <div>
                  <RangeSlider
                    min={HEATING_MIN}
                    max={COOLING_MAX}
                    stepSize={1}
                    labelStepSize={5}
                    value={[editing.heating, editing.cooling]}
                    onChange={this.handleChange("value", setpoint)}
                  />
                </div>
                <div>
                  <Button
                    icon={IconNames.FLOPPY_DISK}
                    intent={Intent.PRIMARY}
                    minimal
                    onClick={() => this.handleSave()}
                  />
                  <Button icon={IconNames.CROSS} intent={Intent.PRIMARY} minimal onClick={() => this.handleCancel()} />
                </div>
              </Card>
            ) : (
              <Card key={setpoint.id || i} interactive>
                <div>{setpoint.label}</div>
                <div>
                  <input className={Classes.INPUT} type="number" value={setpoint.deadband} />
                </div>
                <div>
                  <RangeSlider
                    min={HEATING_MIN}
                    max={COOLING_MAX}
                    stepSize={0.5}
                    labelStepSize={5}
                    value={[setpoint.setpoint - setpoint.deadband / 2, setpoint.setpoint + setpoint.deadband / 2]}
                  />
                </div>
                <div>
                  <RangeSlider
                    min={HEATING_MIN}
                    max={COOLING_MAX}
                    stepSize={1}
                    labelStepSize={5}
                    value={[setpoint.heating, setpoint.cooling]}
                  />
                </div>
                <div>
                  <Button
                    icon={IconNames.EDIT}
                    intent={Intent.PRIMARY}
                    minimal
                    onClick={() => this.handleEdit(setpoint)}
                  />
                  <Button
                    icon={IconNames.TRASH}
                    intent={Intent.WARNING}
                    minimal
                    onClick={() => this.handleDelete(setpoint)}
                    disabled={!isSetpointDelete(setpoint)}
                  />
                </div>
              </Card>
            );
          })}
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state: any) => ({
  setpoints: selectReadSetpoints(state),
  filtered: selectFilterSetpoints(state),
});

const mapActionToProps = { readSetpoints, filterSetpoints, createSetpoint, updateSetpoint, deleteSetpoint };

export default connect(mapStateToProps, mapActionToProps)(Setpoint);
